.header-logo {
  width: 52px;
  height: 52px;
}

.header-logo:hover {
  animation: rumble 0.3s linear;
}

@keyframes rumble {
  25% {
    transform: rotate(10deg);
  }
  75% {
    transform: rotate(-20deg);
  }
}

/*header styling*/
header {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 10px 5px;
  border: 2px solid rgb(240, 3, 3);
  border-radius: 1px;
  background-color: #65539a;
}

nav ul {
  list-style: none;
}

nav ul li {
  display: inline-block;
  text-transform: uppercase;
  font-family: "Press Start 2P", cursive;
  font-size: 1.5vh;
}

/*button click hover effect*/
nav ul li:hover {
  transform: scale(0.9, 0.9);
}

/*Сделал button*/
#button-one {
  background-color: #3bb273;
  padding: 10px 2px;
  border-radius: 10%;
  box-shadow: -3px 3px 3px #339963;
}
/*Работаю button*/
#button-two {
  background-color: #4d9de0;
  padding: 10px 2px;
  border-radius: 10%;
  box-shadow: -3px 3px 3px #2f8dda;
}
/*Учусь button*/
#button-three {
