@media only screen and (min-width: 768px) {
  header {
    justify-content: space-between;
  }

  nav ul li {
    padding: 10px 15px;
    margin-right: 20px;
    font-size: 1em;
  }

  .header-logo {
    padding-left: 10px;
    width: 56px;
    height: 66px;
  }
}

@media only screen and (min-width: 1024px) {
  header {
    justify-content: space-between;
  }

  nav li {
    padding: 10px 15px;
    margin-right: 20px;
    font-size: 1.2em;
  }

  .header-logo {
    padding-left: 20px;
    width: 72px;
    height: 82px;
  }
}

nav ul li a {
  text-decoration: none;
  color: #fff;
}
